export default {
	dateFormat(value){
		var year = value.getFullYear();
		var month = value.getMonth() + 1;
		var date = value.getDate();
		month = month < 10 ? '0' + month : month;
		date = date < 10 ? '0' + date : date;
		return year + '-' + month + '-' + date;
	}
}


