<template>
  <div id="detail" class="animated fadeIn">
    <header class="zdcj-header">
      <mt-navbar class="zdcj-h-nav" v-model="selectedNav">
        <mt-tab-item id="1">7*24</mt-tab-item>
        <mt-tab-item id="2"></mt-tab-item>
        <mt-tab-item id="3"></mt-tab-item>
      </mt-navbar>
      <aside class="zdcj-h-aside">
        
      </aside>
    </header>

    <mt-navbar class="zdcj-news-nav" v-model="selectedType">
      <mt-tab-item id=""></mt-tab-item>
      <mt-tab-item id=""></mt-tab-item>
      <mt-tab-item id=""></mt-tab-item>
      <mt-tab-item id=""></mt-tab-item>
      <mt-tab-item id=""></mt-tab-item>
      <mt-tab-item id=""></mt-tab-item>
      <mt-tab-item id=""></mt-tab-item>
    </mt-navbar>

    <div class="news-banner">
      <mt-swipe :auto="4000">
        <mt-swipe-item><img src="./assets/news-banner.jpg" alt=""></mt-swipe-item>
        <mt-swipe-item><img src="./assets/news-banner.jpg" alt=""></mt-swipe-item>
        <mt-swipe-item><img src="./assets/news-banner.jpg" alt=""></mt-swipe-item>
      </mt-swipe>
    </div>

    <div 
    class="news-list-wrapper"
    v-infinite-scroll="loadMore"
    infinite-scroll-disabled="loading"
    infinite-scroll-distance="1">
      <mt-cell 
      class="news-list"
      v-for="(item, index) in newsArr"
      :data-id="item.id"
      :title="item.title" 
      :label="item.time | dateFormat" 
      :key="index"
      :to="{name: 'newsDetail', params: {name: item.id}}"
      is-link>
        <img class="news-cover" slot="icon" :src="item.imgUrl">
        <div class="news-info">
          <span class="watch" title=""><i class="icon watch-icon"></i>{{item.pageviews}}</span>
          <span class="comment" title=""><i class="icon comment-icon"></i>0</span>
        </div>
      </mt-cell>
      <mt-cell class="loading" v-show="loading">
        <mt-spinner type="triple-bounce" color="#df5000">...</mt-spinner>
      </mt-cell>
    </div>
  </div>
</template>

<script>

import Api from './urlConfig.js'

export default {
  data () {
    return {
      selectedNav: '1',
      selectedType: '',
      msg: '',
      newsArr: [],
      loading: false,
      pageIndex: 0,
      pageSize: 10
    }
  },

  methods: {
    getNewsList(){

      this.sendReq('I0003', {
        OrderBy: "time desc",
        TypeId: '',
        PageIndex: this.pageIndex,
        PageSize: this.pageSize
      })
      .then((res) => {
        var newsList = res.data.data;
        var reg = /\d+/;
        
        this.loading = false;

        if(newsList.length < this.pageSize){
          this.noMore = true;
          this.loading = true;
        }

        newsList.forEach((item, index) => {
          item.time = parseInt(item.time.match(reg)[0]);
          item.time = new Date(item.time);
          this.newsArr.push({
            id: item.Id,
            title: item.title,
            imgUrl: Api.imgUrl + item.pics,
            pageviews: item.praiseCount,
            time: item.time
          });
        })
        
        console.log(this.newsArr);
      })
      .catch((err) => { console.log(err); });
    },
    loadMore() {
      this.loading = true;
      this.pageIndex += 1;
      this.getNewsList();
    }
  },

  created(){
    // this.getNewsList();
  }
}
</script>

<style lang="scss">
  @import "./style/mixin";
  .news-list {
    padding: 10px 0;
  }
  
  .zdcj-header {
    @include nav-style;
    .zdcj-h-nav {
      width: 70%;
      margin-left: 10%;
      background-color: transparent;
      .mint-tab-item {
        &.is-selected {
          color: #fff;
          margin-bottom: 0;
        }
        .mint-tab-item-label {
          font-size: 18px;
        }
      }
    }
    .zdcj-h-aside {
      position: absolute;
      top: 0;
      right: 0;
      width: 20%;
      font-size: 14px;
      padding: 17px 0;
      text-align: center;
    }
  }
  .zdcj-news-nav {
    .mint-tab-item {
      color: #333;
      &.is-selected {
        border-bottom: none;
        margin-bottom: 0;
      }
    }
    .mint-tab-item-label {
      font-size: 14px;
    }
  }
  .news-container {
    margin-bottom: 60px;
  }
  .news-banner {
    height: 200px;
    .mint-swipe-item {
      img {
        width: 100%;
        height: 100%;
      }
    }
    .mint-swipe-indicator {
      background-color: #fff;
      opacity: 1;
      &.is-active {
        background-color: #ff3232;
      }
    }
  }
  .zdcj-tabbar {
    background: none;
    @include nav-style;
  }

  .news-list {
    border-bottom: 1px solid #e9e9e9;
    .mint-cell-wrapper {
      position: relative;
      .mint-cell-title {
        position: relative;
        line-height: 1.5;
        .mint-cell-text {
          display: block;
          padding-left: 120px;
          height: 48px;
          @include multiple-text-break;
        }
      }
    }
    .mint-cell-label {
      padding-left: 120px;
    }
    .mint-cell-value {
      position: absolute;
      bottom: 0;
      right: 10px;
      &.is-link {
        margin-right: 0;
      }
    }
    .news-cover {
      position: absolute;
      left: 0;
      top: 0;
      width: 110px;
      height: 76px;
    }
    .mint-cell-allow-right {
      display: none;
    }
  }

  .news-info {
    display: flex;
    color: #888;
    font-size: 12px;
    line-height: 1.5;
    span {
      position: relative;
      padding-left: 22px;
      flex: 1;
      &.watch {
        margin-right: 5px;
      }
      .icon {
        position: absolute;
        left: 4px;
        top: 3px;
        width: 14px;
        height: 14px;
        background-size: 100%;
        background-position: center;
      }
      .comment-icon {
        background-image: url(./assets/tab-comment.svg);
      }
      .watch-icon {
        background-image: url(./assets/fire.svg);
      }
    }
  }

  .loading {
    border-bottom: none;
    .mint-cell-wrapper {
      justify-content: center;
    }
    .mint-cell-title {
      display: none;
    }
  }
</style>


